$breakpoints: (
    xs: 0,
    sm: hrt.$size-breakpoint-sm,
    md: hrt.$size-breakpoint-md,
    lg: hrt.$size-breakpoint-lg,
    xl: hrt.$size-breakpoint-xl,
    xxl: hrt.$size-breakpoint-2xl,
    xxxl: hrt.$size-breakpoint-3xl,
);

$breakpoint-classes: (xs sm md lg xl xxl xxxl);

$zero-breakpoint: xs !default;

$-breakpoints-keys: value.map-to-list($breakpoints, "keys");

@if list.nth(map.values($breakpoints), 1) != 0 {
    @error 'The first key in the $breakpoints map must have a value of "0".';
} @else {
    $zero-breakpoint: list.nth(map.keys($breakpoints), 1);
}

/// Generates a media query string matching the input value. Refer to the documentation for the `breakpoint()` mixin to see what the possible inputs are.
///
/// @param {Keyword|Number} $val [sm] - Breakpoint name, or px, rem, or em value to process.
@function breakpoint($val: 0) {
    // Size or keyword
    $bp: list.nth($val, 1);
    // Value for max-width media queries
    $bp-max: 0;
    // Direction of media query (up, down, or only)
    $dir: if(list.length($val) > 1, list.nth($val, 2), up);
    // Eventual output
    $str: "";
    // Is it a named media query?
    $named: false;

    // Orientation media queries have a unique syntax
    @if $bp == "landscape" or $bp == "portrait" {
        @return "(orientation: #{$bp})";
    } @else if $bp == "retina" {
        @return "(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)";
    }

    // Try to pull a named breakpoint out of the $breakpoints map
    @if meta.type-of($bp) == "string" {
        @if map.has-key($breakpoints, $bp) {
            @if $dir == "only" or $dir == "down" {
                $bp-max: map-next($breakpoints, $bp);
            }

            $bp: map.get($breakpoints, $bp);
            $named: true;
        } @else {
            $bp: 0;

            @warn 'breakpoint(): "#{$val}" is not defined in your $breakpoints setting.';
        }
    }

    // Convert any pixel, rem, or unitless value to em
    $bp: unit.em-calc($bp);
    // Max value is 0.2px under the next breakpoint (0.02 / 16 = 0.00125).
    // Use a precision under 1px to support browser zoom, but not to low to avoid rounding.
    // See https://github.com/zurb/foundation-sites/issues/11313
    @if $bp-max {
        $bp-max: unit.em-calc($bp-max) - 0.00125;
    }

    // Conditions to skip media query creation
    // - It's a named breakpoint that resolved to "0 down" or "0 up"
    // - It's a numeric breakpoint that resolved to "0 " + anything
    @if $bp > 0em or $dir == "only" or $dir == "down" {
        // `only` ranges use the format `(min-width: n) and (max-width: n)`
        @if $dir == "only" {
            // Only named media queries can have an "only" range
            @if $named == true {
                // Only use "min-width" if the floor is greater than 0
                @if $bp > 0em {
                    $str: $str + "(min-width: #{$bp})";

                    // Only add "and" to the media query if there's a ceiling
                    @if $bp-max {
                        $str: $str + " and ";
                    }
                }

                // Only use "max-width" if there's a ceiling
                @if $bp-max {
                    $str: $str + "(max-width: #{$bp-max})";
                }
            } @else {
                @warn 'breakpoint(): Only named media queries can have an `only` range.';
            }
        }

        // `down` ranges use the format `(max-width: n)`
        @else if $dir == "down" {
            $max: if($named, $bp-max, $bp);

            // Skip media query creation if input value is exactly "0 down",
            // unless the function was called as "sm down", in which case it's just "sm only"
            @if $named or $bp > 0em {
                @if $max {
                    $str: $str + "(max-width: #{$max})";
                }
            }
        }

        // `up` ranges use the format `(min-width: n)`
        @else if $bp > 0em {
            $str: $str + "(min-width: #{$bp})";
        }
    }

    @return $str;
}

/// Wraps a media query around the content you put inside the mixin. This mixin accepts a number of values:
///  - If a string is passed, the mixin will look for it in the `$breakpoints` map, and use a media query there.
///  - If a pixel value is passed, it will be converted to an em value using `hrt.$size-font-body-md` as the base.
///  - If a rem value is passed, the unit will be changed to em.
///  - If an em value is passed, the value will be used as-is.
///
/// @param {Keyword|Number} $value - Breakpoint name, or px, rem, or em value to process.
///
/// @output If the breakpoint is "0px and larger", outputs the content as-is. Otherwise, outputs the content wrapped in a media query.
@mixin breakpoint($value) {
    $str: breakpoint($value);
    $bp: list.index($-breakpoints-keys, $value);
    $pbp: list.index($-breakpoints-keys, lg);

    $old-size: null;

    // Make breakpoint size available as a variable
    @if meta.global-variable-exists(-size) {
        $old-size: $-size;
    }
    $size: list.nth(
        $value,
        1
    ) !global; // get the first value to account for `only` and `down` keywords

    // If $str is still an empty string, no media query is needed
    @if $str == "" {
        @content;
    }

    // Otherwise, wrap the content in a media query
    @else {
        // For named breakpoints less than or equal to $print-breakpoint, add print to the media types
        @if $bp != null and $bp <= $pbp {
            @media print, screen and #{$str} {
                @content;
            }
        } @else {
            @media screen and #{$str} {
                @content;
            }
        }
    }

    $-size: $old-size !global;
}

/// Find the next key in a map.
/// @access private
///
/// @param {Map} $map - Map to traverse.
/// @param {Mixed} $key - Key to use as a starting point.
///
/// @returns {Mixed} The value for the key after `$key`, if `$key` was found. If `$key` was not found, or `$key` was the last value in the map, returns `null`.
@function map-next($map, $key) {
    // Store the keys of the map as a list
    $values: map.keys($map);

    $i: 0;

    // If the Key Exists, Get the index of the key within the map and add 1 to it for the next breakpoint in the map
    @if (map.has-key($map, $key)) {
        $i: list.index($values, $key) + 1;
    }

    // If the key doesn't exist, or it's the last key in the map, return null
    @if ($i > list.length($map) or $i == 0) {
        @return null;
    }
    // Otherwise, return the value
    @else {
        @return map.get($map, list.nth($values, $i));
    }
}

$xs-up: "";
$xs-only: "";

@if map.has-key($breakpoints, xs) {
    $xs-up: screen;
    $xs-only: string.unquote("screen and #{breakpoint(xs only)}");
}

$sm-up: "";
$sm-only: "";

@if map.has-key($breakpoints, sm) {
    $sm-up: string.unquote("screen and #{breakpoint(sm)}");
    $sm-only: string.unquote("screen and #{breakpoint(sm only)}");
}

$md-up: "";
$md-only: "";

@if map.has-key($breakpoints, md) {
    $md-up: string.unquote("screen and #{breakpoint(md)}");
    $md-only: string.unquote("screen and #{breakpoint(md only)}");
}

$lg-up: "";
$lg-only: "";

@if map.has-key($breakpoints, lg) {
    $lg-up: string.unquote("screen and #{breakpoint(lg)}");
    $lg-only: string.unquote("screen and #{breakpoint(lg only)}");
}

$xl-up: "";
$xl-only: "";

@if map.has-key($breakpoints, xl) {
    $xl-up: string.unquote("screen and #{breakpoint(xl)}");
    $xl-only: string.unquote("screen and #{breakpoint(xl only)}");
}

$xxl-up: "";
$xxl-only: "";

@if map.has-key($breakpoints, "xxl") {
    $xxl-up: string.unquote("screen and #{breakpoint(xxl)}");
    $xxl-only: string.unquote("screen and #{breakpoint(xxl only)}");
}

$xxxl-up: "";

@if map.has-key($breakpoints, "xxxl") {
    $xxxl-up: string.unquote("screen and #{breakpoint(xxxl)}");
}
