@use "@gfm-heart/tokens" as hrt;
@use "../utils/breakpoint" as *;
@use "../utils/unit";

.hrt-mx-auto {
    margin-left: auto !important;
    margin-right: auto !important;
}

.hrt-global-wrapper {
    margin: 0 auto;
    max-width: 48rem;
    padding-left: hrt.$size-spacing-2;
    padding-right: hrt.$size-spacing-2;

    @include breakpoint(lg) {
        max-width: 60rem;
    }

    @include breakpoint(xl) {
        max-width: 72rem;
    }
}

/// Grid helpers
.hrt-grid-columns {
    display: grid !important;
    grid-column-gap: hrt.$size-spacing-2 !important;
    grid-template-rows: auto !important;
}

.hrt-grid-columns--2 {
    grid-template-columns: repeat(2, minmax(0, 1fr)) !important;
}

.hrt-grid-columns--3 {
    grid-template-columns: repeat(3, minmax(0, 1fr)) !important;
}

.hrt-grid-columns--4 {
    grid-template-columns: repeat(4, minmax(0, 1fr)) !important;
}

.hrt-grid-columns--2,
.hrt-grid-columns--3,
.hrt-grid-columns--4 {
    &.hrt-grid-columns--for-large {
        @include breakpoint(md down) {
            grid-template-columns: repeat(1, minmax(0, 1fr)) !important;
        }
    }
}

/// Width helpers
.hrt-max-width-full {
    max-width: 100% !important;
}

.hrt-max-width-360 {
    max-width: unit.rem-calc(360) !important;
}

.hrt-max-width-360--for-small {
    @include breakpoint(sm down) {
        max-width: unit.rem-calc(360) !important;
    }
}

.hrt-width-full {
    width: 100% !important;
}

.hrt-width-full--for-small {
    @include breakpoint(sm down) {
        width: 100% !important;
    }
}

.hrt-width-auto {
    width: auto !important;
}

/// Height helpers
.hrt-min-height-100vh {
    min-height: 100vh !important;
}

.hrt-height-100vh {
    height: 100vh !important;
}

.hrt-height-auto {
    height: auto !important;
}

/// Position helpers
.hrt-position-relative {
    position: relative !important;
}

.hrt-position-fixed {
    position: fixed !important;
}

.hrt-position-absolute {
    position: absolute !important;
}

.hrt-position-static {
    position: static !important;
}

/// Flexbox horizontal alignment helpers
.hrt-justify-start {
    justify-content: flex-start !important;
}

.hrt-justify-end {
    justify-content: flex-end !important;
}

.hrt-justify-center {
    justify-content: center !important;
}

.hrt-justify-between {
    justify-content: space-between !important;
}

.hrt-justify-around {
    justify-content: space-around !important;
}

.hrt-justify-evenly {
    justify-content: space-evenly !important;
}

/// Flexbox vertical alignment helpers
.hrt-align-start {
    align-items: flex-start !important;
}

.hrt-align-end {
    align-items: flex-end !important;
}

.hrt-align-center {
    align-items: center !important;
}

.hrt-align-stretch {
    align-items: stretch !important;
}

.hrt-align-baseline {
    align-items: baseline !important;
}

/// Flexbox direction helpers
.hrt-flex-dir-row {
    flex-direction: row !important;
}

.hrt-flex-dir-row-reverse {
    flex-direction: row-reverse !important;
}

.hrt-flex-dir-column {
    flex-direction: column !important;
}

.hrt-flex-dir-column-reverse {
    flex-direction: column-reverse !important;
}

/// Flexbox child helpers
.hrt-align-self-start {
    align-self: flex-start !important;
}

.hrt-align-self-end {
    align-self: flex-end !important;
}

.hrt-align-self-center {
    align-self: center !important;
}

.hrt-align-self-stretch {
    align-self: stretch !important;
}

.hrt-flex-child-auto {
    flex: 1 1 auto !important;
}

.hrt-flex-child-grow {
    flex: 1 0 auto !important;
}

.hrt-flex-child-shrink {
    flex: 0 1 auto !important;
}

.hrt-flex-child-full {
    flex: 0 0 100% !important;
}

/// Flexbox wrap helpers
.hrt-flex-nowrap {
    flex-wrap: nowrap !important;
}

.hrt-flex-wrap {
    flex-wrap: wrap !important;
}

.hrt-flex-wrap-reverse {
    flex-wrap: wrap-reverse !important;
}
