@use "@gfm-heart/tokens" as hrt;
@use "./value" as *;

@mixin text-display-lg {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-display-lg,
        hrt.$size-font-desktop-display-lg
    );
    font-weight: hrt.$font-weight-regular;
    letter-spacing: hrt.$letter-spacing-tight;
    line-height: 1.1;
}

@mixin text-display-md {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-display-md,
        hrt.$size-font-desktop-display-md
    );
    font-weight: hrt.$font-weight-medium;
    letter-spacing: hrt.$letter-spacing-tight;
    line-height: hrt.$line-height-heading;
}

@mixin text-display-sm {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-display-sm,
        hrt.$size-font-desktop-display-sm
    );
    font-weight: hrt.$font-weight-medium;
    letter-spacing: hrt.$letter-spacing-tight;
    line-height: hrt.$line-height-heading;
}

@mixin text-heading-xl {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-heading-xl,
        hrt.$size-font-desktop-heading-xl
    );
    font-weight: hrt.$font-weight-medium;
    letter-spacing: hrt.$letter-spacing-tight;
    line-height: hrt.$line-height-heading;
}

@mixin text-heading-lg {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-heading-lg,
        hrt.$size-font-desktop-heading-lg
    );
    font-weight: hrt.$font-weight-medium;
    letter-spacing: hrt.$letter-spacing-normal;
    line-height: hrt.$line-height-heading;
}

@mixin text-heading-md {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-heading-md,
        hrt.$size-font-desktop-heading-md
    );
    font-weight: hrt.$font-weight-medium;
    letter-spacing: hrt.$letter-spacing-normal;
    line-height: hrt.$line-height-heading;
}

@mixin text-heading-sm {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-heading-sm,
        hrt.$size-font-desktop-heading-sm
    );
    font-weight: hrt.$font-weight-medium;
    letter-spacing: hrt.$letter-spacing-normal;
    line-height: hrt.$line-height-heading;
}

@mixin text-body-lg {
    font-family: hrt.$font-family;
    font-size: fluid-type(
        hrt.$size-font-mobile-body-lg,
        hrt.$size-font-desktop-body-lg
    );
    font-weight: hrt.$font-weight-regular;
    letter-spacing: hrt.$letter-spacing-normal;
    line-height: hrt.$line-height-body;
}

@mixin text-body-md {
    font-family: hrt.$font-family;
    font-size: hrt.$size-font-body-md;
    font-weight: hrt.$font-weight-regular;
    letter-spacing: hrt.$letter-spacing-normal;
    line-height: hrt.$line-height-body;
}

@mixin text-body-sm {
    font-family: hrt.$font-family;
    font-size: hrt.$size-font-body-sm;
    font-weight: hrt.$font-weight-regular;
    letter-spacing: hrt.$letter-spacing-normal;
    line-height: hrt.$line-height-body;
}

@mixin text-uppercase {
    font-feature-settings: "ss08", "case";
    text-transform: uppercase;
}

@mixin text-mono {
    font-family: hrt.$font-family-mono;
    font-feature-settings: normal;
}
