/* ----- Modals ----- */
:root {
  --hrt-link-color: currentColor;
}

*,
::before,
::after {
  border-width: 0; /* 2 */
  box-sizing: inherit; /* 1 */
  transition-duration: inherit; /* 3 */
  transition-property: inherit; /* 3 */
  transition-timing-function: inherit; /* 3 */
}

html {
  box-sizing: border-box;
  color: #333333;
  font-family: "CircularXXWeb", Trebuchet, Helvetica, Arial, sans-serif; /* 4 */
  font-feature-settings: "liga", "ss08"; /* 5 */
  font-size: 16px; /* 1 */
  line-height: 1.5; /* 1 */
  -moz-tab-size: 4; /* 3 */
  tab-size: 4; /* 3 */
  -webkit-text-size-adjust: 100%; /* 2 */
  transition-duration: 0.2s; /* 6 */
  transition-property: border-color, opacity, transform; /* 6 */
  transition-timing-function: cubic-bezier(0.38, 0.41, 0.27, 1); /* 6 */
}

body {
  background-color: #ffffff;
  color: inherit;
  font-size: inherit; /* 2 */
  line-height: inherit; /* 2 */
  margin: 0; /* 1 */
  overflow-x: hidden;
}

hr {
  border-top-style: solid;
  border-top-width: 1px; /* 2 */
  color: #c8c8c8;
  height: 0; /* 1 */
}

abbr:where([title]) {
  text-decoration: underline dotted;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

a {
  color: var(--hrt-link-color);
  cursor: pointer;
  text-decoration: underline;
}
a:hover {
  color: var(--hrt-link-color);
  text-decoration: none;
}

b,
strong {
  font-weight: 800;
}

code,
kbd,
samp {
  font-family: ui-monospace, Menlo, Monaco, "Cascadia Mono", "Segoe UI Mono", "Roboto Mono", "Oxygen Mono", "Ubuntu Monospace", "Source Code Pro", "Fira Mono", "Droid Sans Mono", "Courier New", monospace;
  font-feature-settings: normal;
  /* 1 */
}

code:not(pre code),
kbd,
samp {
  background-color: #f8f8f8;
  border: 1px solid #e4e4e4;
  border-radius: 0.25rem;
  color: #767676;
  display: inline-block;
  font-size: 0.825em;
  padding: 0 0.35em;
}

pre {
  font-family: ui-monospace, Menlo, Monaco, "Cascadia Mono", "Segoe UI Mono", "Roboto Mono", "Oxygen Mono", "Ubuntu Monospace", "Source Code Pro", "Fira Mono", "Droid Sans Mono", "Courier New", monospace;
  font-feature-settings: normal;
  background-color: #f8f8f8;
  font-size: 0.875rem;
  overflow: auto; /* 1 */
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: inherit;
  vertical-align: baseline;
}

sub {
  font-feature-settings: "subs";
}

sup {
  font-feature-settings: "sups";
}

table {
  border-collapse: collapse; /* 3 */
  border-color: inherit; /* 2 */
  text-indent: 0; /* 1 */
}

button,
input,
optgroup,
select,
textarea {
  color: inherit; /* 1 */
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  font-weight: inherit; /* 1 */
  line-height: inherit; /* 1 */
  margin: 0; /* 2 */
  padding: 0; /* 3 */
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button; /* 1 */
}

:-moz-focusring {
  outline: auto;
}

:-moz-ui-invalid {
  box-shadow: none;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

summary {
  display: list-item;
}

blockquote,
dl,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
figure,
p,
pre {
  margin: 0;
}

fieldset {
  margin: 0;
  padding: 0;
}

legend {
  padding: 0;
}

ol,
ul,
menu {
  margin: 0;
}

menu,
nav ul {
  list-style: none;
  padding: 0;
}

textarea {
  border-radius: 0;
  height: auto; /* 2 */
  resize: vertical; /* 1 */
}

input::placeholder,
textarea::placeholder {
  color: #adadad; /* 2 */
  opacity: 1; /* 1 */
}

button,
[role=button] {
  cursor: pointer;
}

:disabled {
  cursor: default;
}

img,
svg,
video,
canvas,
audio,
iframe,
embed,
object {
  display: block; /* 1 */
  vertical-align: middle; /* 2 */
}

img,
video {
  height: auto;
  max-width: 100%;
}

[hidden] {
  display: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 1rem;
  margin-top: 0;
}

iframe {
  max-width: 100%;
}

.hrt-text-gray {
  --hrt-link-color: #333333;
}

input {
  min-width: 0;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
  text-align: right;
}

dt {
  float: left;
}

select {
  width: 100%;
}

