.hrt-show-for-sr,
.hrt-show-on-focus {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  white-space: nowrap;
  width: 1px;
}

.hrt-show-on-focus:active, .hrt-show-on-focus:focus {
  clip: auto;
  height: auto;
  overflow: visible;
  position: static !important;
  white-space: normal;
  width: auto;
}

.hrt-opacity-0 {
  opacity: 0 !important;
}

.hrt-callout {
  border-radius: 0.25rem;
  height: max-content;
  padding: 1.25rem;
}

.hrt-callout--sand {
  background-color: #fbf8f6;
}

.hrt-callout--gray {
  background-color: #f1f1f1;
}

.hrt-callout--green {
  background-color: #e6f6ef;
}

.hrt-callout--blue {
  background-color: #e7f0f7;
}

.hrt-icon {
  color: inherit;
  display: inline-block;
  flex-shrink: 0;
  /*
   * ensure click events trigger on SVGs with <use> elements
   * See https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/10583086/
   */
  pointer-events: none;
  vertical-align: text-top;
}

.hrt-icon--small {
  height: 1rem;
  width: 1rem;
}

.hrt-icon--large {
  height: 1.5rem;
  width: 1.5rem;
}

.hrt-icon--tab {
  height: 1.75rem;
  width: 1.75rem;
}

.hrt-icon--xlarge {
  height: 2.5rem;
  width: 2.5rem;
}

.hrt-icon--disabled {
  filter: grayscale(1);
  opacity: 0.6;
}

.hrt-spot-icon {
  align-items: center;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
}

.hrt-spot-icon--large {
  height: 2.5rem;
  width: 2.5rem;
}
.hrt-spot-icon--large .hrt-icon {
  height: 1.5rem;
  width: 1.5rem;
}

.hrt-spot-icon--small {
  height: 1.875rem;
  width: 1.875rem;
}
.hrt-spot-icon--small .hrt-icon {
  height: 1rem;
  width: 1rem;
}

.hrt-spot-icon--blue {
