@use "@gfm-heart/tokens" as hrt;
@use "../utils/breakpoint" as *;
@use "../utils/visibility";

/// Display helpers
.hrt-disp-block {
    display: block !important;
}

.hrt-disp-flex {
    display: flex !important;
}

.hrt-disp-grid {
    display: grid !important;
}

.hrt-disp-inherit {
    display: inherit;
}

.hrt-disp-inline {
    display: inline !important;
}

.hrt-disp-inline-block {
    display: inline-block !important;
}

.hrt-disp-inline-flex {
    display: inline-flex !important;
}

.hrt-disp-none {
    display: none !important;
}

@mixin hrt-visibility-classes {
    .hrt-invisible {
        visibility: hidden;
    }

    /// Responsive visibility classes
    @each $size in $breakpoint-classes {
        @if $size != $zero-breakpoint {
            .hrt-hide-for-#{$size} {
                @include visibility.hide-for($size);
            }

            .hrt-show-for-#{$size} {
                @include visibility.show-for($size);
            }
        }

        .hrt-hide-for-#{$size}-only {
            @include visibility.hide-for-only($size);
        }

        .hrt-show-for-#{$size}-only {
            @include visibility.show-for-only($size);
        }
    }

    /// Screen reader visibility classes
    /// Need a "hide-for-sr" class? Add aria-hidden='true' to the element
    .hrt-show-for-sr,
    .hrt-show-on-focus {
        @include visibility.element-invisible;
    }

    /// Only display the element when it's focused
    .hrt-show-on-focus {
        &:active,
        &:focus {
            @include visibility.element-invisible-off;
        }
    }
}

@include hrt-visibility-classes;

.hrt-opacity-0 {
    opacity: 0 !important;
}
