@use "@gfm-heart/tokens" as hrt;
@use "@gfm-heart/styles/utils" as hrt-utils;

.hrt-spot-icon {
    align-items: center;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
}

.hrt-spot-icon--large {
    height: hrt-utils.rem-calc(40);
    width: hrt-utils.rem-calc(40);

    .hrt-icon {
        height: hrt-utils.rem-calc(24);
        width: hrt-utils.rem-calc(24);
    }
}

.hrt-spot-icon--small {
    height: hrt-utils.rem-calc(30);
    width: hrt-utils.rem-calc(30);

    .hrt-icon {
        height: hrt-utils.rem-calc(16);
        width: hrt-utils.rem-calc(16);
    }
}

.hrt-spot-icon--blue {
    background-color: hrt.$color-blue-10;
    color: hrt.$color-blue-dark;
}

.hrt-spot-icon--green {
    background-color: hrt.$color-green-10;
    color: hrt.$color-green-dark;
}

.hrt-spot-icon--gold {
    background-color: hrt.$color-gold-10;
    color: hrt.$color-gold-dark;
}

.hrt-spot-icon--gray {
    background-color: hrt.$color-gray-10;
    color: hrt.$color-gray-dark;
}

.hrt-spot-icon--orange {
    background-color: hrt.$color-orange-10;
    color: hrt.$color-orange-dark;
}

.hrt-spot-icon--purple {
    background-color: hrt.$color-purple-10;
    color: hrt.$color-purple-dark;
}

.hrt-spot-icon--red {
    background-color: hrt.$color-red-10;
    color: hrt.$color-red-dark;
}
