@use "@gfm-heart/tokens" as hrt;
@use "@gfm-heart/styles/utils" as hrt-utils;

.hrt-callout {
    border-radius: hrt.$size-radius-1;
    height: max-content;
    padding: hrt.$size-spacing-1 * 2.5;
}

.hrt-callout--sand {
    background-color: hrt.$color-sand;
}

.hrt-callout--gray {
    background-color: hrt.$color-gray-10;
}

.hrt-callout--green {
    background-color: hrt.$color-green-10;
}

.hrt-callout--blue {
    background-color: hrt.$color-blue-10;
}
