@use "@gfm-heart/tokens" as hrt;
@use "@gfm-heart/styles/utils" as hrt-utils;

.hrt-icon {
    color: inherit;
    display: inline-block;
    flex-shrink: 0;
    /*
     * ensure click events trigger on SVGs with <use> elements
     * See https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/10583086/
     */
    pointer-events: none;
    vertical-align: text-top;
}

.hrt-icon--small {
    height: hrt-utils.rem-calc(16);
    width: hrt-utils.rem-calc(16);
}

.hrt-icon--large {
    height: hrt-utils.rem-calc(24);
    width: hrt-utils.rem-calc(24);
}

.hrt-icon--tab {
    height: hrt-utils.rem-calc(28);
    width: hrt-utils.rem-calc(28);
}

.hrt-icon--xlarge {
    height: hrt-utils.rem-calc(40);
    width: hrt-utils.rem-calc(40);
}

.hrt-icon--disabled {
    filter: grayscale(1);
    opacity: 0.6;
}
